<template>
    <div class="page">
        <h1>Story Maker - Editor</h1>
        <span class="buttons">
            <button @click="downloadStory" class="button">Save</button>
            <UploadFile v-model="uploadStory" :text="true" id="uploadStory" />
            <button @click="$router.push('/play')" class="button">Play</button>
            <button @click="$router.push('/')" class="button">Back</button>
        </span>
        
        <span class="field">
            <label>Story title</label>
            <input type="text" v-model="story.title" />
        </span>

        <h2>Scenes</h2>
        <p>{{ story.scenes ? story.scenes.length : 0 }} scenes</p>
        <ul>
            <li v-for="(scene, index) in story.scenes" :key="index">{{ scene.name }}</li>
        </ul>
        <button class="button" @click="$router.push('/edit/scenes')">Edit Scenes</button>

        <h2>Characters</h2>
        <p>{{ story.characters ? story.characters.length : 0 }} characters</p>
        <ul>
            <li v-for="(character, index) in story.characters" :key="index">{{ character.name }}</li>
        </ul>
        <button class="button" @click="$router.push('/edit/characters')">Edit Characters</button>

        <h2>Plan</h2>
        <p>{{ story.plan ? story.plan.length : 0 }} steps in plan</p>
        <button class="button" @click="$router.push('/edit/plan')">Edit Plan</button>
    </div>
</template>

<script>
import UploadFile from "@/components/UploadFile.vue";

export default {
    name: 'Editor',
    components: { UploadFile },
    data() {
        return {
        uploadStory: "",
        story: {}
        }
    },
    mounted() {
        this.story = this.$store.getters.story;
    },
    watch: {
        uploadStory(uploadedStory) {
            this.story = JSON.parse(uploadedStory);
            this.$store.commit("update", this.story);
        }
    },
    methods: {
        downloadStory() {
            this.download(JSON.stringify(this.story), "my", "story");
        },
        download(data, filename, type) {
            var file = new Blob([data], {type: type});
            if (window.navigator.msSaveOrOpenBlob) // IE10+
                window.navigator.msSaveOrOpenBlob(file, filename);
            else { // Others
                var a = document.createElement("a"),
                url = URL.createObjectURL(file);
                a.href = url;
                a.download = filename;
                document.body.appendChild(a);
                a.click();
                setTimeout(function() {
                    document.body.removeChild(a);
                    window.URL.revokeObjectURL(url);  
                }, 0); 
            }
        }
    }
}
</script>

