
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

export default new Vuex.Store({
    state: {
      story: {
        scenes: [],
        characters: [],
        plan: []
      }
    },
    getters: {
      story(state) {
        return state.story;
      }
    },
    mutations: {
      update(state, story) {
          state.story = story;
      }
    }
  })