<template>
    <div class="page">
        <h1>Story Maker</h1>
        <p>Loaded Story: {{ story.title }}</p>
        <button @click="$router.push('/edit')" class="wideMenu button">Edit</button>
        <button @click="$router.push('/play')" class="wideMenu button">Play</button>
    </div>
</template>

<script>
export default {
    data() {
        return {
            story: {}
        }
    },
    mounted() {
        this.story = this.$store.getters.story;
    }
}
</script>